import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import CSSModules from "react-css-modules";

class Test extends Component {
  constructor(props) {
    super(props);
    document.title = '';
    this.state = {};
  }
  render() {
    return (
      <ul>
        <li>
          <Link to="/UniversityChoice"></Link>
        </li>
        <li>
          <Link to="/Register"></Link>
        </li>
        <li>
          <Link to="/StudentInformationDetails"></Link>
        </li>
        <li>
          <Link to="/PrimaryStudentInformation"></Link>
        </li>
        <li>
          <Link to="/PaySuccess"></Link>
        </li>
        <li>
          <Link to="/ParentInformation"></Link>
        </li>
        <li>
          <Link to="/ParentHome"></Link>
        </li>
        <li>
          <Link to="/OrderDetails"></Link>
        </li>
        <li>
          <Link to="/MyOrder"></Link>
        </li>
        <li>
          <Link to="/MedicalInsurance"></Link>
        </li>
        <li>
          <Link to="/ConfirmOrder"></Link>
        </li>
        <li>
          <Link to="/CommonProblem"></Link>
        </li>
        <li>
          <Link to="/CollegeStudentHome"></Link>
        </li>
        <li>
          <Link to="/ApplyRefund"></Link>
        </li>
        <li>
          <Link to="/AlreadyRefund"></Link>
        </li>
        <li>
          <Link to="/Agreement"></Link>
        </li>
        <li>
          <Link to="/AdvancedStudentInformation">
            
          </Link>
        </li>
        <li>
          <Link to="/OrderCompleted"></Link>
        </li>
        <li>
          <Link to="/ChoosingRoles"></Link>
        </li>
        <li>
          <Link to="/PrimaryStudentRegister"></Link>
        </li>
      </ul>
    );
  }
}

export default Test;



