import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Divider from "@material-ui/core/Divider";
class AlreadyRefund extends Component {
  constructor(props) {
    super(props);
    document.title = "";
    this.state = {
      insuranceList: [
        {
          time: {
            fistTime: "2019-08-08 08:00:00",
            lastTime: "2019-08-08 08:00:00"
          },
          price: 260
        },
        {
          time: {
            fistTime: "2019-08-08 08:00:00",
            lastTime: "2019-08-08 08:00:00"
          },
          price: 260
        }
      ]
    };
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="AlreadyRefund">
        <div styleName="AlreadyRefund_top">
          <div styleName="AlreadyRefund_top_content">
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title"></span>
              <span styleName="AlreadyRefund_top_item_content">
                L1254887956325545
              </span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title"></span>
              <span styleName="AlreadyRefund_top_item_content">260</span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title"></span>
              <span styleName="AlreadyRefund_top_item_content"></span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title"></span>
              <span styleName="AlreadyRefund_top_item_content"></span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title"></span>
              <span styleName="AlreadyRefund_top_item_content">
                2301558948954415
              </span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title"></span>
              <span styleName="AlreadyRefund_top_item_content">
                13945687412
              </span>
            </div>
          </div>
        </div>
        {this.state.insuranceList.map((v, i) => {
          return (
            <div styleName="AlreadyRefund_top1" key={i}>
              <div styleName="AlreadyRefund_top_item">
                <span styleName="AlreadyRefund_top_item_title1">
                  
                </span>
                <Divider style={{ backgroundColor: "#B5B5B6" }} />
              </div>
              <div styleName="AlreadyRefund_top_item">
                <span styleName="AlreadyRefund_top_item_title"></span>
                <div styleName="AlreadyRefund_top_item_time">
                  <span styleName="AlreadyRefund_top_item_content1">
                    &nbsp;{v.time.fistTime}
                  </span>
                  <span styleName="AlreadyRefund_top_item_content1">
                    &nbsp;{v.time.lastTime}
                  </span>
                </div>
                <Divider style={{ backgroundColor: "#B5B5B6" }} />
              </div>
              <div styleName="AlreadyRefund_top_item">
                <span styleName="AlreadyRefund_top_item_title"></span>
                <span styleName="AlreadyRefund_top_item_content">
                  {v.price}
                </span>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default CSSModules(AlreadyRefund, styles);



