import Register from '../Register'; //
import PrimaryStudentInformation from '../PrimaryStudentInformation'; //
import AdvancedStudentInformation from '../AdvancedStudentInformation'; //
import ParentInformation from '../ParentInformation'; //
import ParentHome from '../ParentHome'; //
import CollegeStudentHome from '../CollegeStudentHome'; //
import StudentInformationDetails from '../StudentInformationDetails'; //
import MedicalInsurance from '../MedicalInsurance'; //
import ConfirmOrder from '../ConfirmOrder'; //
import MyOrder from '../MyOrder'; //
import AlreadyRefund from '../AlreadyRefund'; //
import OrderDetails from '../OrderDetails'; //
import ApplyRefund from '../ApplyRefund'; //
import CommonProblem from '../CommonProblem'; //
import PaySuccess from '../PaySuccess'; //
import Agreement from '../Agreement'; //
import OrderCompleted from '../OrderCompleted'; //
import UniversityChoice from '../Components/UniversityChoice'; //
import ChoosingRoles from "../ChoosingRoles"//
import PrimaryStudentRegister from "../PrimaryStudentRegister"
import Test from '../Test'; //
// import AuthRouter from "../AuthRouter"; //
// import App from "../App";

export default [
  { path: '/', name: 'Test', component: Test, auth: true },
  { path: '/Register', name: 'Register', component: Register },
  {
    path: '/PrimaryStudentInformation',
    name: 'PrimaryStudentInformation',
    component: PrimaryStudentInformation,
    auth: true
  },
  {
    path: '/AdvancedStudentInformation',
    name: 'AdvancedStudentInformation',
    component: AdvancedStudentInformation,
    auth: true
  },
  {
    path: '/ParentInformation',
    name: 'ParentInformation',
    component: ParentInformation,
    auth: true
  },
  {
    path: '/ParentHome',
    name: 'ParentHome',
    component: ParentHome,
    auth: true
  },
  {
    path: '/CollegeStudentHome',
    name: 'CollegeStudentHome',
    component: CollegeStudentHome,
    auth: true
  },
  {
    path: '/StudentInformationDetails',
    name: 'StudentInformationDetails',
    component: StudentInformationDetails,
    auth: true
  },
  {
    path: '/MedicalInsurance',
    name: 'MedicalInsurance',
    component: MedicalInsurance,
    auth: true
  },
  {
    path: '/ConfirmOrder',
    name: 'ConfirmOrder',
    component: ConfirmOrder,
    auth: true
  },
  { path: '/MyOrder', name: 'MyOrder', component: MyOrder, auth: true },
  {
    path: '/AlreadyRefund',
    name: 'AlreadyRefund',
    component: AlreadyRefund,
    auth: true
  },
  {
    path: '/OrderDetails',
    name: 'OrderDetails',
    component: OrderDetails,
    auth: true
  },
  {
    path: '/ApplyRefund',
    name: 'ApplyRefund',
    component: ApplyRefund,
    auth: true
  },
  {
    path: '/CommonProblem',
    name: 'CommonProblem',
    component: CommonProblem,
    auth: true
  },
  {
    path: '/PaySuccess',
    name: 'PaySuccess',
    component: PaySuccess,
    auth: true
  },
  { path: '/Agreement', name: 'Agreement', component: Agreement, auth: true },
  { path: '/Test', name: 'Test', component: Test },
  {
    path: '/OrderCompleted',
    name: 'OrderCompleted',
    component: OrderCompleted,
    auth: true
  },
  {
    path: '/UniversityChoice',
    name: 'UniversityChoice',
    component: UniversityChoice,
    auth: true
  },{
    path: '/ChoosingRoles',
    name: 'ChoosingRoles',
    component: ChoosingRoles,
    auth: true
  },{
    path: '/PrimaryStudentRegister',
    name: 'PrimaryStudentRegister',
    component: PrimaryStudentRegister,
    auth: true
  }
];



