import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Button from "@material-ui/core/Button";

class CommonProblem extends Component {
  constructor(props) {
    super(props);
    document.title = "";
    this.state = {
      problemList: [
        {
          problem: ":",
          answer:
            ""
        },
        {
          problem: ":",
          answer:
            ""
        },
        {
          problem: ":",
          answer:
            ""
        },
        {
          problem: ":",
          answer:
            ""
        },
        {
          problem: ":",
          answer:
            ""
        },
        {
          problem: ":",
          answer:
            ""
        }
      ]
    };
  }

  render() {
    return (
      <div styleName="CommonProblem">
        <div>
          {this.state.problemList.map((v, i) => {
            return (
              <div styleName="CommonProblem_item" key={i}>
                <div styleName="CommonProblem_item_problem">{v.problem}</div>
                <div styleName="CommonProblem_item_answer">{v.answer}</div>
              </div>
            );
          })}
        </div>
        <Button
          variant="contained"
          style={{
            color: "#fff",
            display: "block",
            margin: "0 auto"
          }}
          color="primary"
          styleName="CommonProblem_submitBox_button"
          onClick={() => {
            window.location.href = "tel:15362532197";
          }}
        >
          &nbsp;400-877-7777
        </Button>
        <div styleName="CommonProblem_bottomBlock" />
      </div>
    );
  }
}

export default CSSModules(CommonProblem, styles);



