import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Divider from '@material-ui/core/Divider';
import Radio from '@material-ui/core/Radio';
import Button from '@material-ui/core/Button';
import alreadyPurchased from '../images/alreadyPurchased.png';

class MedicalInsurance extends Component {
  constructor(props) {
    super(props);
    document.title = '';
    this.state = {
      isSelectProduct: false,
      isCheck: false,
      checkList: [],
      timerList:[],
      changeColor:[],
      InsuranceList: [
        {
          title: '',
          index: 0,
          status: '',
          time: {
            firstTime: '2019-09-09 08:00:00',
            lastTime: '2019-09-09 08:00:00'
          },
          tips: '91',
          Cost: 300
        },
        {
          title: '()',
          status: '',
          index: 1,
          time: {
            firstTime: '2019-09-09 08:00:00',
            lastTime: '2019-09-09 08:00:00'
          },
          Cost: 300
        }
      ]
    };
  }

  // linkTo = () => {
  //   this.props.history.push('/App');
  // };
  changeIsCheck = () => {
    this.setState({
      isCheck: !this.state.isCheck
    });
  };
  showAgreement = ()=>{
    let index = true
    this.state.checkList.map((v,i)=>{
      if(v === true){
        console.log("=====")
        index = false
      }
      return false
    })
    console.log(this.state.checkList)
    if(index){
      alert('')
    }else{
      this.props.history.push("/Agreement")
      this.setState({
      isCheck: !this.state.isCheck
    });
    }
  }
  isChoice = index => {
    let list = this.state.checkList;
    // isSelectProduct
    list[index] = !list[index];
    if (list.indexOf(true) === -1) {
      this.setState({
        checkList: list,
        isSelectProduct: false
      });
    } else {
      this.setState({
        checkList: list,
        isSelectProduct: true
      });
    }
  };
  submit = ()=>{
    if(!this.state.isSelectProduct && !this.state.isCheck){
      return false;
    }
    this.props.history.push("/ConfirmOrder")
  }
  componentDidMount() {
    let checkList = [];
    let timerList = [];
    let changeColor = [];
    // let timer1 = setInterval(()=>{
    //   console.log("timer1")
    // },200)
    let _this = this;
    this.state.InsuranceList.map((v, i) => {
      checkList[i] = false;
      changeColor[i] = true
      timerList[i] = setInterval(()=>{
        changeColor[i] = !changeColor[i]
        _this.setState({
          changeColor : changeColor
        })
        console.log(_this.state.changeColor)
      },900)
      return false;
    });
    console.log(timerList)
    this.setState({
      checkList: checkList,
      timerList:timerList
    });
  }
  componentWillUnmount() {
    this.state.timerList.map((v,i)=>{
      clearInterval(v);
      return false;
    })
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="MedicalInsurance">
        {this.state.InsuranceList.map((v, i) => {
          return (
            <div styleName="MedicalInsurance_content" key={i}>
              <div
                styleName="MedicalInsurance_iconBox"
                onClick={() => {
                  this.isChoice(i);
                }}
              >
                <div
                  styleName="triangle-topright"
                  style={{
                    // '#D5D3FC transparent transparent #D5D3FC'
                    // '#B5B5B6 transparent transparent #B5B5B6'
                    // '#009B63 transparent transparent #009B63'
                    borderColor: this.state.checkList[i]
                      ? '#009B63 transparent transparent #009B63' : this.state.changeColor[i] ? '#D5D3FC transparent transparent #D5D3FC' : '#B5B5B6 transparent transparent #B5B5B6'
                  }}
                />
                <span className="iconfont icon-dui" styleName="editIcon" />
              </div>
              <div styleName="MedicalInsurance_infoBox">
                <p styleName="MedicalInsurance_infoBox_title">{v.title}</p>
                <div styleName="MedicalInsurance_infoBox_stauts">
                  <img
                    styleName="MedicalInsurance_infoBox_stauts_img"
                    src={alreadyPurchased}
                    alt="status"
                  />
                </div>
                <div styleName="MedicalInsurance_infoBox_timeSpan">
                  <div styleName="MedicalInsurance_infoBox_timeSpan_line" />
                  
                  <div styleName="MedicalInsurance_infoBox_timeSpan_line1" />
                </div>
                <div styleName="MedicalInsurance_infoBox_time">
                  {v.time.firstTime}  <span>{v.time.lastTime}</span>
                </div>
                {v.tips && (
                  <div styleName="MedicalInsurance_infoBox_tips">{v.tips}</div>
                )}
                <div styleName="MedicalInsurance_infoBox_timeSpan">
                  <div styleName="MedicalInsurance_infoBox_timeSpan_line" />
                  
                  <div styleName="MedicalInsurance_infoBox_timeSpan_line1" />
                </div>
                <div styleName="MedicalInsurance_infoBox_Cost">
                  
                  <span styleName="MedicalInsurance_infoBox_CostSpan">
                    {v.Cost}
                  </span>
                  .00
                </div>
                <div styleName="MedicalInsurance_infoBox_timeSpan">
                  <div styleName="MedicalInsurance_infoBox_timeSpan_line" />
                  
                  <div styleName="MedicalInsurance_infoBox_timeSpan_line1" />
                </div>
                <div styleName="briefIntroduction">
                  
                </div>
                <Divider style={{ backgroundColor: '#B5B5B6' }} />
                <div styleName="MedicalInsurance_infoBox_button"></div>
              </div>
            </div>
          );
        })}
        <div styleName="MedicalInsurance_submitBox">
          <div styleName="selectBox" onClick={this.showAgreement}>
            <Radio
              checked={this.state.isCheck}
              onClick={() => {
                this.changeIsCheck();
              }}
              color="primary"
              value="c"
              icon={<div styleName="selectIcon" />}
              checkedIcon={
                <div styleName="selectIcon1">
                  <span
                    className="iconfont icon-shuruzhengque"
                    styleName="selectIcon1_gou"
                  />
                </div>
              }
            />
            <span></span>
          </div>
          <Button
            variant="contained"
            style={{
              color: '#fff',
              textDecoration: 'none',
              backgroundColor: this.state.isSelectProduct&&this.state.isCheck
                ? '#009B63'
                : '#B5B5B6'
            }}
            styleName="MedicalInsurance_submitBox_button"
            onClick={this.submit}
          >
            
          </Button>
        </div>
      </div>
    );
  }
}

export default CSSModules(MedicalInsurance, styles);



